@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary-color: #2d033b;
  --secondary-color: #810ca8;
  --Tertiary-color: #c147e9;
  --quaternary-color: #e5b8f4;
}

*,
::after,
::before {
  box-sizing: border-box;
}

body {
  background: #2b2b31; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #2b2b31,
    #222281
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-family: "Poppins", sans-serif;
  margin: 0;
  color: #fff;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
}

/* header { */
/* padding: 1rem; */
/* display: flex; */
/* justify-content: space-between; */
/* } */
.movie {
    height: 400px;
    margin: 5px;
    /* background-color: var(--secondary-color); */
    box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
    border-radius: 10px;
    cursor: pointer;
    float: left;
    width: 19%;
    background-size: cover;
    background-position: center;
    outline: 0;
    /* padding: 5px; */
  }
  
  .movie a {
    text-decoration: none;
    color: #fff;
    background-color: transparent;
  }
  
  .movie:hover {
    box-shadow: 0 4px 5px rgba(0, 0, 0, 0.6);
    border: 2px solid #fff;
  }
  
  .movie img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: .3s all ease
  }
  
  .movie:hover img {
    opacity: 0.5; 
  }
  
  .movie-info {
    position: absolute;
    bottom: 0;
    text-align: center;
    padding: 0.5rem 1rem;
    width: 100%;
    height: 60px;
    background: linear-gradient(
      0deg,
      rgba(6, 9, 19, 0.83) 0%,
      rgba(6, 9, 19, 0.65) 65%,
      rgba(12, 19, 39, 0.18) 80%,
      rgba(12, 20, 40, 0.11) 10%,
      rgba(17, 28, 58, 0) 96%
    );
    /* background-position: top; */
    z-index: 9;
  }
  
  .movie-info h3 {
    position: relative;
    font-size: 0.8rem;
    z-index: 0;
    margin: 0 auto;
    top: 1rem;
    /* padding-bottom: 12px; */
    /* width: 66.6666%; */
    /* top: -20px; */
    overflow: hidden;
    text-align: center;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
  }
  
  .quality {
    position: absolute;
    left: 0;
    top: 1rem;
    -webkit-transform: rotate(-45deg) translateX(-35px) translateY(-20px);
            transform: rotate(-45deg) translateX(-35px) translateY(-20px);
    font-size: 0.8rem;
    background-color: #f11f1f;
    width: 150px;
    text-align: center;
    line-height: 1.5rem;
  }
  
  .rate-star {
    position: absolute;
    background-color: #ffd34c;
    color: #222;
    font-weight: bold;
    font-size: 0.8rem;
    top: 1rem;
    right: -4rem;
    opacity: 0;
    padding: 2px 5px;
    border-radius: 5px;
    transition: all 0.2s linear;
  }
  
  .movie:hover .rate-star {
    opacity: 1;
    right: 5px;
  }
  
  .genres {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 4px;
    gap: 4px;
    position: absolute;
    bottom: -3rem;
    opacity: 0;
    transition: all 0.2s linear;
  }
  
  .movie:hover .genres {
    bottom: 3rem;
    opacity: 1;
  }
  
  .genres span {
    padding: 0 10px;
    font-size: 0.8rem;
    background-color: #ffd34c;
    color: #222;
    border-radius: 50px;
  }
  
  .play-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 4rem;
    opacity: 0;
    transition: all 0.2s linear;
  }
  
  .movie:hover .play-btn {
    opacity: 1;
  }
  
  .movie-info .views {
    position: absolute;
    bottom: -5px;
  }

  
@media screen and (max-width: 1018px) {
    .movie {
      width: 31.33%;
    }
  
    .movie img {
      width: 100%;
      height: 100%;
      object-fit: fill;
      /* position: absolute; */
    }
  }
  
  @media screen and (max-width: 766px) {
    .movie {
      width: 47%;
    }
  }
  
  @media screen and (max-width: 540px) {
    .movie {
      width: 95%;
      height: 510px;
    }
  }
.container {
  position: relative;
  display: block;
  margin: 0 auto;
  max-width: 1200px;
}

.pagination {
    display: inline-block;
    margin-top: 2rem;
  }
  
  .pagination-list {
    list-style-type: none;
  }
  
  .pagination-list li {
    display: inline-block;
    /* padding: 0.5rem 1rem; */
    margin-right: 10px;
  }
  
  /* .page__numbers {
  } */
  
  .page__numbers a {
    color: #fff;
    text-decoration: none;
    width: 100%;
    padding: 0.5rem 1rem;
  }
  
  .page__numbers.active {
    background-color: var(--secondary-color);
    color: white;
    border-radius: 5px;
  }
  
  .page__numbers:hover:not(.active) {
    background-color: var(--secondary-color);
    border-radius: 5px;
  }
  
  .no-page {
    color: #fff;
  }

  @media screen and (max-width: 540px) {  
    .pagination {
      position: relative;
      right: 2rem;
    }
    .pagination-list li {
      font-size: 0.8rem;
      padding: 0.3rem 0.5rem;
    }
  }
nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;

  width: 100%;
  height: 60px;
  /* display: flex;
    justify-content: space-between; */
  font-size: 1rem;
}

.nav-transparent {
  background-color: transparent;
}

.nav-colored {
  background: linear-gradient(to right, #444447, #181896);
}

nav ul {
  position: absolute;
  top: 0;
  left: 100px;
  list-style-type: none;
  padding-left: 30px;
}

nav ul li {
  text-transform: uppercase;
  margin-left: 20px;
  display: inline-block;
}

nav a {
  color: var(--quaternary-color);
  text-decoration: none;
  padding-bottom: 10px;
}

nav a:hover {
  color: #e6e6e6;
  /* font-weight: bold; */
  border-bottom: solid #fff;
}

.prand-icon a:hover {
  border: 0;
}

.prand-icon_image {
  height: 60px;
}

#form {
  position: absolute;
  top: 5px;
  right: 5rem;
}

.search {
  background-color: transparent;
  border: 2px solid var(--primary-color);
  border-radius: 50px 0 0 50px;
  font-family: inherit;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  color: #fff;
}

.search::-webkit-input-placeholder {
  color: var(--quaternary-color);
}

.search::placeholder {
  color: var(--quaternary-color);
}

.search:focus {
  outline: none;
  background-color: var(--primary-color);
}

.icon {
  position: absolute;
  color: #e5b8f4;
  display: none;
  background-color: var(--secondary-color);
  padding: 10px 20px;
  font-size: 2rem;
  border: 0;
  height: 60px;
  z-index: 2;
  top: 0;
  right: 0;
}

.icon:hover {
  color: var(--secondary-color);
  display: inline-block;
  background-color: #da90f3;
  cursor: pointer;
}

.search-icon {
  position: absolute;
  color: #e5b8f4;
  display: none;
  background-color: var(--secondary-color);
  padding: 10px 20px;
  font-size: 2rem;
  border: 0;
  height: 60px;
  top: 0;
  right: 80px;
}

.search-icon:hover {
  color: var(--secondary-color);
  display: inline-block;
  background-color: #da90f3;
  cursor: pointer;
}

@media screen and (max-width: 930px) {
  nav ul {
    padding-left: 0;
    position: relative;
    width: 100%;
    text-align: center;
    transition: all 0.6s ease-in;
    -webkit-transform: translate(-50%, -600px);
            transform: translate(-50%, -600px);
    background-color: #8d51a1;
    /* margin-top: 112px; */
    padding-top: 1rem;
    padding-bottom: 0.5rem;
    margin: 0;
  }

  nav ul[data-state="opened"] {
    -webkit-animation: openMenu 0.6s ease-in forwards;
            animation: openMenu 0.6s ease-in forwards;
    display: block;
  }

  nav ul[data-state="closing"] {
    -webkit-animation: closeMenu 0.6s ease-in forwards;
            animation: closeMenu 0.6s ease-in forwards;
  }

  nav ul[data-state="closed"] {
    display: none;
  }

  nav ul li {
    display: block;
    margin-bottom: 20px;
  }

  nav a {
    color: var(--quaternary-color);
    text-decoration: none;
    padding-bottom: 10px;
    width: 100%;
  }

  nav a:hover {
    color: #e6e6e6;
    /* font-weight: bold; */
    border-bottom: solid #fff;
  }

  #form {
    display: flex;
    justify-content: center;
    position: absolute;
    top: 60px;
    left: 50%;
    width: 100%;
    height: 100%;
    height: 100%;
    background: linear-gradient(to right, #444447, #181896);
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    padding: 5px;
    /* width: 0; */
    /* transition: ; */
  }

  .search {
    background-color: transparent;
    border-radius: 50px 0 0 50px;
    font-family: inherit;
    font-size: 1rem;
    color: #fff;
    border: 2px solid var(--primary-color);
    padding: 0.5rem 1rem;
    flex-grow: 0.5;
  }

  .show-search {
    display: block;
  }

  .icon {
    display: block;
  }

  .search-icon {
    display: block;
  }

  .hide {
    display: none !important;
  }

  .show {
    /* position: absolute; */
    top: 70px;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    opacity: 1;
  }

  @-webkit-keyframes openMenu {
    0% {
      position: absolute;
      -webkit-transform: translate(-90px, -600px);
              transform: translate(-90px, -600px);
      opacity: 0;
    }

    100% {
      top: 70px;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      opacity: 1;
    }
  }

  @keyframes openMenu {
    0% {
      position: absolute;
      -webkit-transform: translate(-90px, -600px);
              transform: translate(-90px, -600px);
      opacity: 0;
    }

    100% {
      top: 70px;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      opacity: 1;
    }
  }

  @-webkit-keyframes closeMenu {
    0% {
      top: 70px;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      opacity: 1;
    }

    100% {
      position: absolute;
      -webkit-transform: translate(-90px, -600px);
              transform: translate(-90px, -600px);
      opacity: 0;
    }
  }

  @keyframes closeMenu {
    0% {
      top: 70px;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      opacity: 1;
    }

    100% {
      position: absolute;
      -webkit-transform: translate(-90px, -600px);
              transform: translate(-90px, -600px);
      opacity: 0;
    }
  }
}

.search-btn {
  cursor: pointer;
  border: 2px solid var(--primary-color);
  background: var(--primary-color);
  text-align: center;
  color: #fff;
  border-radius: 0 50px 50px 0;
  cursor: pointer;
  font-family: inherit;
  font-size: 1rem;
  color: #fff;
  padding: 0.5rem 1rem;
}

a {
  color: #fff;
  text-decoration: none;
}

.details-container {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 60px;
  background-color: #6db3f2;
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url(/my-app/static/media/background.88757002.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-basis: 70%;
}

.movie-poster {
  width: 30%;
  align-self: flex-start;
  /* padding: 2rem; */
}

.movie-poster img {
  border: 5px solid #fff;
  border-radius: 5px;
  width: 100%;
}

.movie-poster a {
  display: block;
}

.basic-btn {
  background-color: #de5073;
  text-align: center;
  cursor: pointer;
  border-radius: 2px;
  text-transform: capitalize;
  padding: 10px;
}

.basic-btn.download-btn {
  background-color: var(--secondary-color);
  margin-top: 5px;
}

.movie-details {
  display: flex;
  flex-direction: column;
  font-weight: bold;
  margin-left: 10%;
  align-self: flex-start;
  padding-left: 20px;
  width: 70%;
}

.movie-title h1 {
  margin: 0;
  margin-bottom: 2rem;
  padding: 0;
  line-height: 3rem;
  font-size: 3rem;
  text-shadow: 0 2px 2px hsla(0, 0%, 0%, 0.4);
}

.movie-title h2 {
  font-size: 1.5rem;
  line-height: 8px;
}

.movie-quality {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 6px;
  gap: 6px;
  margin-top: 30px;
  margin-bottom: 30px;
}

.movie-quality a {
  background-color: #1b1b2366;
  border: 1px solid #ffffff29;
  padding: 5px 12px;
}

.rating {
  margin-bottom: 10px;
  white-space: nowrap;
}

.icon-rating {
  display: inline-block;
  position: relative;
  width: 75px;
  margin-right: 30px;
  background-color: transparent;
}

.icon-rating img {
  position: relative;
  top: 3px;
}

.movie-likes {
  font-size: 1.2em;
}

.icon-rating .heart {
  font-size: 20px;
}

.heart {
  color: #de5073;
}

.keywords {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 6px;
  gap: 6px;
  margin-top: 2rem;
}

.keyword {
  background-color: #1b1b2366;
  border: 1px solid #ffffff29;
  padding: 5px 12px;
  white-space: nowrap;
}

.mobile-movie-title {
  display: none;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 3rem 0;
  overflow: hidden;
}

.media_panel {
  display: flex;
  width: 80%;
  grid-gap: 1%;
  gap: 1%;
}

.media_panel * {
  width: 33%;
  object-fit: cover;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.6);
}

.iframe-container {
  position: relative;
  overflow: hidden;
  padding-top: 30%;
}

.responsive-iframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.overview-wrapper {
  display: flex;
  justify-content: center;
  width: 100%;
}

.actors {
  display: flex;
  width: 80%;
  overflow: hidden;
  /* padding: 6rem; */
  grid-gap: 5%;
  gap: 5%;
}

.overview {
  flex: 2 1;
}

.cast {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}

.actor {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  margin-top: 1rem;
}

/* .actor:nth-of-type(2) {
    border-bottom: solid 1px #240b0b;
    padding-bottom: 1rem;
  } */

.actor img {
  border-radius: 50%;
  height: 100px;
  width: 100px;
  object-fit: cover;
  object-position: center;
}

.actor h4 {
  margin-left: 1rem;
}

/* Medai Query sections */

@media screen and (max-width: 1223px) {
  body {
    font-size: 0.8rem;
  }
  .movie-title h1 {
    margin: 0;
    margin-bottom: 1rem;
    padding: 0;
    line-height: 2rem;
    font-size: 2rem;
    text-shadow: 0 2px 2px hsla(0, 0%, 0%, 0.4);
  }

  .movie-title h2 {
    font-size: 1rem;
    line-height: 4px;
  }
}

@media screen and (max-width: 950px) {
  .details-container {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    padding: 10px;
    background-position: center;
  }

  .movie-details {
    font-size: 0.8em;
    margin-left: 2px;
  }

  .movie-poster {
    align-self: flex-start;
    width: 50%;
  }

  .movie-poster img {
    min-width: 100px;
  }

  .movie-quality {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    order: 2;
  }

  .icon-rating {
    width: 40px;
    margin-right: 20px;
  }

  .keywords {
    /* flex-direction: column; */
    align-items: flex-start;
    order: 3;

    margin-top: 0.5rem;
  }

  .movie-title {
    display: none;
  }

  .mobile-movie-title {
    display: block;
    width: 100%;
    flex-grow: 1;
  }

  .mobile-movie-title h1 {
    line-height: 2rem;
    font-size: 1.5rem;
    text-shadow: 0 2px 2px hsla(0, 0%, 0%, 0.4);
  }

  .mobile-movie-title h2 {
    font-size: 1rem;
    line-height: 8px;
  }

  .media_panel {
    flex-direction: column;
    align-items: center;
  }

  .media_panel img {
    width: 80%;
    object-fit: cover;
    margin-top: 1rem;
  }

  .iframe-container {
    width: 80%;
    padding-top: 50%;
  }
}

@media screen and (max-width: 700px) {
  .actors {
    flex-direction: column;
  }
}

